@import "./vars";
@import "./media";

*, *::before, *::after {
	box-sizing: border-box; }

html, body, .root {
	width: 100%;
	height: 100%; }

body {
	font-family: $base-ff;
	font-size: 16px;
	line-height: 1;
	font-weight: normal;
	background: $background; }

h1,h2,h3,h4,h5 {
	margin: 0; }

ul {
	list-style: none;
	padding: 0; }

.root {
	overflow-x: hidden; }

.container {
	max-width: 1440px;
	padding: 0 100px;
	margin: 0 auto;
	@include lg {
		padding: 0 60px; }
	@include sm {
		padding: 0 30px; }
	@include xs {
		padding: 0 20px; } }

.row {
	display: flex;
	margin-bottom: 100px;
	@include lg {
		margin-bottom: 40px; } }

